
.my-projects {
  display: flex;
  align-items: center;
  font-size: 2rem;
  width: 100vw;
  height: 85vh;
  position: relative;
  scroll-snap-type: x mandatory;
  scroll-snap-type: y mandatory;

  overscroll-behavior: none;
  flex-wrap: nowrap;
  z-index: 3;
  overflow-x: hidden;
  overflow-y: hidden;
  &::-webkit-scrollbar {
    display: none;
  }
}
